import TitleComponent from "../../../../components/Title/TitleComponent"
import {useEffect, useState} from "react";
import Button from "@mui/material/Button";
import {InputFileImage} from "../../../../components/Input/InputFileImage";
import {useDispatch, useSelector} from "react-redux";
import {CloudUploadOutlined, PasswordRounded} from "@mui/icons-material";
import InputText from "../../../../components/Input/InputText";
import {
  fetchUsersAction,
  fetchChangeImagesAction,
  fetchChangePasswordUsersAction
} from "../../../../app/service/action/usersAction";
import Loading from "../../../../components/Loading/Loading";

const INITIAL_ACCOUNT_OBJ = {
  email: '',
  password: '',
  retryPassword: ''
}

const Account = () => {
  const dispatch: any = useDispatch()
  const [loading, setLoading] = useState(false);
  const [requestParam, setRequestParams] = useState(INITIAL_ACCOUNT_OBJ)
  const access_token = localStorage.getItem('token');
  const userDetail = JSON.parse(localStorage.getItem('userDetail'));
  const [image, setImage] = useState({
    image: ''
  });

  const [imageUrl, setImageUrl] = useState(null);

  const [error, setError] = useState('')
  const updateFormValue = ({updateType, value}) => {
    // setErrorMessage("")
    setRequestParams({...requestParam, [updateType]: value})
  }

  const updateImageValue = ({updateType, value}) => {
    // setErrorMessage("")
    setImage({...image, [updateType]: value})
    const url = URL.createObjectURL(value);
    setImageUrl(url);
  }
  const isLoading = useSelector(({usersState}) => usersState.users.isLoading);
  const data = useSelector(({usersState}) => usersState.users.data);
  const status = useSelector(({usersState}) => usersState.users.status);

  useEffect(() => {
    dispatch(fetchUsersAction(userDetail.id, access_token));
  }, [dispatch, userDetail.id, access_token]);

  useEffect(() => {
    if (status === 'ready' && data.result) {
      setRequestParams({
        email: data.result.email,
        password: data.result.password,
        retryPassword: data.result.password,
      })
    }
  }, [data.result, status]);

  const handleChangeImage = () => {
    setLoading(true)
    dispatch(fetchChangeImagesAction(image)).then((res) => {
      setLoading(false)
    })
  }

  const handleChangePassword = () => {
    setLoading(true)
    if (requestParam.password !== requestParam.retryPassword) {
      setError("password don't match!!!")
      setLoading(false)
    } else {
      setError('')
      dispatch(fetchChangePasswordUsersAction(requestParam.password))
      // setLoading(false)
    }
  }
  return (
      <div>
        <TitleComponent title={'Pengaturan Akun'}/>
        {isLoading ? (<Loading/>)
            : loading ? <Loading/> :
                <div className={'account'}>
                  <div className="account-content">
                    <div className={'image-account'}>
                      <img src={imageUrl ? imageUrl : data.result.image} alt=""/>
                    </div>
                    <InputFileImage
                        updateType={'image'}
                        accept={'image/*'}
                        label={''}
                        type={'file'}
                        updateFormValue={updateImageValue}/>

                    <InputText label={'Email'} disabled={true}
                               defaultValue={userDetail.username}
                               updateType="email"
                               updateFormValue={updateFormValue}/>
                  </div>
                  <Button size={'small'} variant="contained"
                          startIcon={<CloudUploadOutlined/>}
                          onClick={handleChangeImage}
                  >
                    Update Image
                  </Button>
                  <hr/>
                  <div className="account-content">
                    <InputText labelTitle={'Password'} type={'password'}
                               updateType={'password'}
                               placeholder={'Mininam 8 Karakter'}
                               updateFormValue={updateFormValue}/>

                    <InputText labelTitle={'Ulang Password'} type={'password'}
                               updateType={'retryPassword'}
                               placeholder={'Mininam 8 Karakter'}
                               updateFormValue={updateFormValue}/>
                    <small className={'error-message'}>{error}</small>
                  </div>
                  <Button size={'small'} variant="contained"
                          onClick={handleChangePassword}
                          startIcon={<PasswordRounded/>}>
                    Change Password
                  </Button>
                </div>
        }
      </div>
  )
}

export default Account